<template>
  <div>
    <van-sticky>
      <van-nav-bar title="邀请码" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <div class="codeBg">
      <div id="qrcode"></div>
      <div class="codeShow">
        <div class="codeText">
          我的邀请码
          <div class="inviteNo">{{ inviteCode }}</div>
        </div>
        <button
          id="copyBtn"
          data-clipboard-action="copy"
          :data-clipboard-text="copyContent"
        >
          复制邀请码
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { myQrcode } from "@/api/personal";
import { userInfo } from "@/api/personal";
import Clipboard from "clipboard";
import QRCode from "qrcodejs2"; // 引入qrcode
export default {
  data() {
    return {
      inviteCode: "",
      copyContent: ""
    };
  },
  created() {
    this.$toast.loading({
      message: "正在生成二维码...",
      mask: true,
      duration: 4000
    });
    myQrcode().then(res => {
      this.inviteCode = res.qrcode;
      userInfo().then(res => {
        this.copyContent = `http://${window.location.host}/h5/#/mall?uuid=${this.inviteCode}`;
        let qrcode = new QRCode(document.getElementById("qrcode"), {
          width: 152,
          height: 152,
          text: `http://${window.location.host}/h5/#/mall?uuid=${this.inviteCode}` // 二维码内容
        });
      });
    });
  },
  mounted() {
    const clipboard = new Clipboard("#copyBtn");
    let _this = this;
    clipboard.on("success", function() {
      _this.$toast("复制成功");
    });
    clipboard.on("error", function() {
      this.$toast("复制失败");
    });
  },

  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="scss" scoped>
.codeBg {
  width: 100%;
  position: absolute;
  height: 100vh;
  left: 0;
  bottom: 0;
  background: url(../../assets/imgs/personal/inviteBg.png) 0 0 no-repeat;
  background-size: 100% 100%;
  text-align: center;
}
#qrcode {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border: 1px solid #fff;

  top: 80px;
  img {
    width: 152px;
    height: 152px;
    padding: 4px;
  }
}
.codeShow {
  margin-top: 240px;
  color: #fff;
  .codeText {
    font-size: 15px;
    .inviteNo {
      font-size: 18px;
      margin-top: 12px;
    }
  }
}
#copyBtn {
  color: #fff;
  width: 124px;
  height: 30px;
  line-height: 30px;
  background: #28A6FF;
  border: 0 none;
  font-size: 14px;
  border-radius: 5px;
  padding: 0;
}
</style>
